.filters {
  display: flex;
  flex-direction: column;
  position: fixed;
  text-align: center;
  top:4rem;
  width: 14rem;
  height: 100vh;
  padding: 1rem;
  z-index: 1;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.categoryFilter {
  text-align: left;
}


.categorySelect {
  width: 100%;
  padding:.5rem;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  border: none;
}


@media only screen and (max-width: 900px) {
  .filters {
    display: none;
  }
}


@media only screen and (max-width: 1200px) and (min-width: 900px) {
  .filters {
    display: flex;
    flex-direction: column;
    position: fixed;
    text-align: center;
    top:2rem;
    width: 10rem;
    height: 100vh;
    padding: 1rem;
    z-index: 1;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  }
}
